.listPanel {
  grid-area: listPanel;
  background-color: white;
  color: #66347F;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.listView{
  text-align: center;
  overflow: hidden;
  height: 100%;
  width: 100%;
  min-height: 350px;
  padding: 20px;
}

.listContainer {
  display: flex;
  flex-flow: column nowrap;
  align-items: start;
  justify-content: center;
  width: 9fr;
  padding: 20px;
  row-gap: 30px;
  overflow: auto;
}

.listStart{
    /* margin-top: 75px; */
    width: 100px;
    height: 100px;
    background-color: black;
    color: white;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
}

.controlPanel {
  background-color: #C8EFFC;
  box-sizing: border-box;
}

.actionButton{
  width: 100%;
  border:none;
  border-radius: 2px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  background-color: #d9d9d9;
}

input[type="checkbox"]{
  width: 5px;
  height: 5px;
}

fieldset{
  border: 1px solid red;
  display: flex;
  flex-direction: row;
  align-items: center;
}

fieldset label{
  /* width:50px; */
}

.controlPanel{
  width: 100%;
  padding: 15px;
}

.controlPanel input{
  height: 32px;
  border-radius: 2px;
  width: 118px;
  padding: 10px;
}

