.controlPanel {
  grid-area: controlPanel;
  /* background-color: #37306B; */
  background: linear-gradient(145deg, #D27685 0%, #9E4784 25%, #66347F 50%, #37306B 75%);
  display: flex;
  flex-direction: row;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  gap: 20px;
  color: white;
  font-weight: 700;
  border-top: 3px solid grey;
  box-sizing: border-box;
}

@keyframes debounce {
  from {
    transform: scale(0.8);
  }

  to {
    transform: scale(1);
  }
}

.controlActionContainer {
  display: flex;
  flex-flow: row wrap;
  min-width: 50px;
  width: 100%;
  justify-content: space-around;
  box-sizing: border-box;
}

.actionCard {
  box-sizing: border-box;
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.actionCard .header {
  font-size: large;
  font-weight: 700;
  height: 40px;
  margin: 0;
  padding: 10px;
  box-sizing: border-box;

}

.actionCard .body {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  box-sizing: border-box;
  margin: 20px 0;
  row-gap: 10px;
  column-gap: 5px;
}

.actionCard .body input{
  width: 200px;
  height: 60px;
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  box-sizing: border-box;
  background-color: white;
  color: #66347F;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
}

.actionCard .body input:focus,
.actionCard .body select:focus {
  outline: none;
}

.actionCard .body input:disabled {
  background-color: grey;
  opacity: 0.5;
  pointer-events: none;
}

.actionCard .body button {
  background-color: lightgrey;
  height: 40px;
  padding: 5px;
  box-sizing: border-box;
  border-radius: 10px;
  width: 150px;
  font-weight: 600;
  background-color: rgba(25, 143, 25, 0.975);
  color: white;
}

